.mission-page {
  margin: 15px;
  padding: 50px 50px 50px 50px;
  background-color: white; /* Imposta lo sfondo bianco per la sezione home-page */
}


.section-mission{
  
  padding: 95px 0 120px;
  opacity: 1;
  background-color: rgb(255, 255, 255);
  border-radius: 20px;
}


.container-mission{
  margin: 0 auto;
  max-width: 1400px;
}

.title-row-mission{
  
  font-size: 25px;
  opacity: 1;
}



.para-row-mission{
  box-shadow: 0 2px 8px #0b0e1129;
  border-radius: 8px;
  padding:30px 50px;
}

.font-span-mission {
  color: #f0b90b;
  font-weight: 600;
}

.features-section-mission {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; /* Mantiene lo spazio tra le feature */
  margin: 0 -10px; /* Regola i margini per allineare al bordo esterno */
  margin-bottom: 60px; /* Spazio extra sotto le feature */
}

.feature-mission {
  flex-basis: calc(25% - 20px); /* Utilizza flex-basis per una migliore gestione della dimensione */
  margin: 10px;
  border: 2px solid #333;
  border-radius: 25px;
  padding: 20px;
  box-sizing: border-box; /* Include padding e bordo nella dimensione */
}

/* Adatta le feature per schermi medi (es. tablet) */
@media (max-width: 1023px) and (min-width: 601px) {
  .feature-mission {
    flex-basis: calc(50% - 20px); /* Due feature per riga */
  }
}

/* Adattamento per schermi piccoli (smartphone) */
@media (max-width: 600px) {
  .feature-mission {
    flex-basis: 100%; /* Una feature per riga, occupa l'intera larghezza */
    margin: 10px 0; /* Adegua i margini per il layout in colonna */
  }
}

