/* Importa un font da Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+Devanagari:wght@100;200;300;400;500;600;700&display=swap');

body, html {
  margin: 0;
  padding: 0;
  font-family: 'IBM Plex Sans Devanagari', sans-serif;
}

h1, h2, p {
  margin-bottom: 1em;
}

.home-page, .features-section, .section, .home-header, .feature, .container, .row-flex, .col-md, .col-md-one, .col-md-box, .header-content, nav ul, nav li, .header-logo {
  box-sizing: border-box;
}

.home-page {
  margin: 15px;
  padding: 20px;
  background-color: white;
}

.home-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  margin: 45px auto;
  border-radius: 15px;
  background-image: url('./icona-1.webp');
  background-size: cover;
  background-position: center;
}

.header-content {
  width: 40%;
  min-height: 200px;
  box-shadow: 0 2px 8px #0b0e1129;
  background-color: #f0f0f0;
  padding: 20px;
  margin: 0 20px;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.header-logo{
  opacity: 0.2;
  max-width: 100%;
  height: auto;
  border-radius: 20px;
}

nav ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  background-color: #ffffff;
  overflow: hidden;
}

nav li {
  float: left;
  background-color: #ffffff; /* Sfondo rosso chiaro per ogni elemento li */
  border-radius:20px ;
}

nav li a {
  display: block;
  color: rgb(3, 3, 3);
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  box-shadow: 0 2px 5px rgba(0,0,0,0.2);
}

nav li a:hover {
  background-color: #ddd;
  color: whitesmoke;
}

.menu-fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
  background: #fff;
  box-shadow: 0 2px 5px rgba(0,0,0,0.2);
}

.hamburger-menu {
  display: none;
}

@media (max-width: 768px) {
  .hamburger-menu {
    display: flex;
    cursor: pointer;
    flex-direction: column;
    justify-content: space-around;
    width: 40px;
    height: 30px;
    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 1000;
  }

  .hamburger-menu div {
    height: 4px;
    width: 100%;
    background-color: #333;
    border-radius: 10px;
    transition: all 0.3s linear;
  }

  .nav-links {
    display: none;
    position: fixed;
    top: 60px;
    left: 0;
    width: 100%;
    background-color: #fff;
    box-shadow: 0 2px 5px rgba(0,0,0,0.2);
    z-index: 999;
  }

  .nav-links.open {
    display: flex;
    flex-direction: column;
  }

  .nav-links li {
    text-align: center;
    border-bottom: 1px solid #ddd; /* Aggiunge un separatore visivo tra gli elementi */
  }

  .nav-links li:last-child {
    border-bottom: none; /* Rimuove il bordo dall'ultimo elemento */
  }

  .nav-links li a {
    color: #333;
  }

  .header-content, .header-logo, h1, p {
    width: 100%;
    margin: 10px 0;
  }

  h1 {
    font-size: 1.5em;
  }

  p {
    font-size: 1em;
  }
}

/* Stili per il footer */
footer {
  background-color: #131212;
  color: white;
  text-align: center;
  padding: 10px 0;
  position: fixed;
  bottom: 0;
  width: 100%;
}


.features-section {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around; /* Cambiato da 'space-between' a 'space-around' per migliorare l'allineamento */
  margin: 0 -10px;
  margin-bottom: 60px;
}

.feature {
  flex-basis: calc(33.333% - 20px); /* Usa 'flex-basis' invece di 'width' per una gestione flex pi√π accurata */
  margin: 10px;
  border: 2px solid #333;
  border-radius: 25px;
  padding: 20px;
  box-sizing: border-box;
}

/* Aggiustamento per schermi grandi dove non si vuole che le feature vadano a capo */
@media (min-width: 1024px) {
  .feature {
    flex-basis: calc(33.333% - 20px); /* Assicura che tre feature riempiano la riga */
  }
}

/* Regolazioni per schermi medi (tablet, ecc.) */
@media (max-width: 1023px) and (min-width: 601px) {
  .feature {
    flex-basis: calc(50% - 20px); /* Due feature per riga */
  }
}

/* Regolazioni per schermi piccoli (smartphone, ecc.) */
@media (max-width: 600px) {
  .feature {
    flex-basis: 100%; /* Una feature per riga */
  }
}

.feature img {
  width: 100%;
  max-width: 200px; /* Adatta questa dimensione in base al layout desiderato */
  border-radius: 10px;
  margin-bottom: 10px;
}

.feature h2, .feature p {
  margin: 10px 0; /* Assicura un margine uniforme per titoli e paragrafi */
}

/*prima sezione della terza sezione hompage*/

.section {
  padding: 95px 0 120px;
  opacity: 1;
  background-color: rgb(255, 255, 255);
  border-radius: 20px;
  
}

.container {
  margin: 0 auto;
  max-width: 1400px;
  
}


.title-row {
  text-align: center!important;
  font-size: 40px;
  opacity: 1;
}

.para-row {
  box-shadow: 0 2px 8px #0b0e1129;
  border-radius: 8px;
  padding:30px 50px;
}

.font-span {
  color: #f0b90b;
  font-weight: 600;
}

.row-mt {
  margin-top: 5.5rem;
  display: flex;
  flex-wrap: wrap; /* Assicura che gli elementi vadano a capo su schermi piccoli */
}

.col-12, .col-14 {
  box-shadow: 0 2px 8px #0b0e1129;
  border-radius: 8px;
  padding: 30px 20px; /* Riduci il padding sui lati per dispositivi mobili */
  flex: 0 0 100%; /* Fa occupare l'intera larghezza su schermi piccoli */
  margin-bottom: 20px; /* Aggiunge margine inferiore per spaziare gli elementi */
}

.col-13 {
  margin: 0 auto 20px; /* Centra l'immagine e aggiunge margine inferiore */
  display: flex;
  justify-content: center;
  width: 100%; /* Occupa l'intera larghezza */
}

@media (min-width: 768px) {
  .col-12 { width: 38.66666667%; }
  .col-13 { 
    margin-right: 25px;
    margin-left: 25px;
    width: 16.66666667%;
  }
  .col-14 { width: 41.66666667%; }
}
